@import '../../utils';

.bag-item-container {
  width: 96%;
  display: flex;
  height: 100%;
  margin: 0 10px 10px 10px;
  padding-bottom: 4px;
  border-bottom: 0.5px solid rgb(197, 197, 197);
  font-family: $body;

  a {
    color: rgb(197, 197, 197);
  }

  img {
      width: 16%;
  }

  .bag-item-description-container {
    width: 290px;
    padding-top: 12px;
    display: flex;
    font-size: 12px;
    text-align: start;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin:0px 7px;

    span {
      margin: 3px 0px;
    }

  }

  .bg-it-remove-button {
    font-size: 10px;
    cursor: pointer;
    width: 25px;
  }
}